.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px; /* Set the fixed height of the footer here */
    line-height: 60px; /* Vertically center the text there */
    color: rgba(240, 222, 200, 1) !important;
  }
  .footer p {
    margin-bottom: 0;
  }
.fa {
    padding: 10px;
    font-size: 20px;
    text-align: center;
    text-decoration: none;
    margin: 5px 2px;
    border-radius: 30%;
}
.fa:hover {
    opacity: 0.5;
    color: rgba(240, 222, 200, 1);
}
.fa-linkedin {
    background: #007bb5;
    color: rgba(240, 222, 200, 1);
    opacity:.9;
}
.fa-github {
    background: black;
    color: rgba(240, 222, 200, 1);
    opacity:.9;
}


